#modal-remote {
  .modal {
    &-content {
      position: relative;

      & > .close {
        position: absolute;
        right: 10px;
        top: 10px;
      }

      & > iframe {
        width: 100%;
        height: 300px;
        height: 80vh;
        border: none;
      }
    }
  }
}


